import React, { Component } from "react";
import formState from "./ItemFormState";
import FormsHelper from "../Forms/FormsHelper";
import FormElementComponent from "../Forms/FormElement/FormElementComponent";
import ButtonComponent from "../Forms/Button/ButtonComponent";
import manageItemsClasses from "./ManageItemsComponent.css";

class ManageItemsComponent extends Component {
    state = formState;
    formElementChangedHandler = (event, inputIdentifier) => {
        const updatedForm = {
          ...this.state.formData
        };
        const updatedFormElement = {
          ...updatedForm[inputIdentifier]
        };
        updatedFormElement.value = event.target.value;
        updatedFormElement.valid = FormsHelper.checkValidity(
          updatedFormElement.value,
          updatedFormElement.validation
        );
        updatedFormElement.touched = true;
        updatedForm[inputIdentifier] = updatedFormElement;
    
        let formIsValid = true;
        for (let inputIdentifier in updatedForm) {
          formIsValid = updatedForm[inputIdentifier].valid && formIsValid;
        }
        this.setState({
          formData: updatedForm,
          formIsValid: formIsValid,
          formTouched: true
        });
      };
    
      submitHandler = event => {
        event.preventDefault();
        console.log(this.state.customFormData);
      };
    render() {
        let formElementsArray = [];
        for(let key in this.state.formData) {
            formElementsArray.push({
                id: key,
                config: this.state.formData[key]
            });
        }
        
        let form = (
            <form onSubmit={this.submitHandler}>
                {formElementsArray.map(formElement => (
                    <FormElementComponent
                        key={formElement.id}
                        elementType={formElement.config.elementType}
                        elementConfig={formElement.config.elementConfig}
                        value={formElement.config.value}
                        invalid={!formElement.config.valid}
                        shouldValidate={formElement.config.validation}
                        touched={formElement.config.touched}
                        changed={event =>
                        this.formElementChangedHandler(event, formElement.id)
                        }
                    />
                ))
                }
                <ButtonComponent
                    disabled={!this.state.formIsValid || !this.state.formTouched}
                    >
                    Submit
                </ButtonComponent>
            </form>
        );
        return (
            <div className={manageItemsClasses.ManageItemWrapper}>
                <div className={manageItemsClasses.FormWrapper}>
                    <div className={manageItemsClasses.FormImgSection}>
                        <img className={manageItemsClasses.FormImg} src="https://lh3.googleusercontent.com/9kcrHjK5EpTpJYKvPcnxl87nIY7ReASe-O_uBvzhFvjuZ3-y9Xe0UG2h-djLajZUlvi224htwrHRbC7rnavqqWhJaQe_oeyqlhqT0s55Ms4hjlY67scLl3l9y4v0Od0TyLMrCk8RHSYGcsKdQmG3hSqhLr1WbQfQhJlg0kd-hJ2RW1jWLTim5Uv92W8rnPVSExfZXUFp6VqAR7so9107W3kgA6OFZBnVE3O_R8qsdoYGGzCr2zuQBb5am-kk4rOQN__p1jAeL_utrObvL7PL7RKYGP__VGE5am0EKCXD9O_H_qgKdY15KbQc-JVilifW6SXu768J8PFNY9_fShqipJsQRlqdpDxCSSlfotAveGJ1u715K8j8nF2KjWRv88m4DWuiM_QQ_baiUNtlw8xLf6r1c_dtw14lDJ87CmreP7AIGVkdDifOA6Ia_DsMEJb6irc_4f2eFa3vJX8mB_-JEpyMnG6xHH-a8AdSpueKyU5-VBIJBbGVPgYSptsSViJmgBe4xB-ShYvxaPcOew_ZbpG1KHMHCrB0aCX_Onh_kPAbGGaCOQfHRbBrtJySI1WMH8j2fG3MvCr0He7NI_qAtAjDC5rLmm29SPmhol2jfPfB3FASdnPQXsxV7zgpMdESJTagvZmFXQAFCB8Imr7X_HlMMs4EReKJZS36MNOvNTtwdbL45dHV0sr-D89chg=w435-h847-no">
                        </img>
                    </div>
                    <div className={manageItemsClasses.FormSection}>
                        <h3>Add</h3>
                        {form}
                    </div>
                </div>
            </div>

        );
    }
}

export default ManageItemsComponent;



